.Sidebar {
  flex: 1;
  /* border-right: 1px solid  rgb(230, 227, 227); */
  min-height: 100vh;
  .top {
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;

    
    

    .logo {
      font-size: 20px;
      font-weight: bold;
      color: white;
      
    }
  }

  hr {
    height: 0;
    border: 0.5px solid rgb(230, 227, 227);
  }
  .center {
    padding-left: 20px;
    padding-right: 20px;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      .title {
        font-size: 10px;
        font-weight: bold;
        color: #999;
        margin-top: 15px;
        margin-bottom: 5px;
      }
      li {
        display: flex;
        align-items: center;
        padding: 5px;
        cursor: pointer;
        &:hover {
          background-color: #ece8ff;

          border-radius: 10px;
            .Icon{
                color:#1e1e1e;
            }
            span{
                color:#1e1e1e;
            }
            .link{
                color:#1e1e1e;
            }


        }

        .Icon {
          font-size: 25px;
          color: white;
        }
        span {
          font-size: 13px;
          font-weight: 600;
          color: white;
          margin-left: 10px;
        }
      }
    }
  }

  .bottom {
    display: flex;
    align-items: center;
    margin: 10px;

    .colorOption {
      width: 20px;
      height: 20px;
      border-radius: 5px;
      border: 1px solid #7451f8;
      cursor: pointer;
      margin: 5px;

      &:nth-child(1) {
        background-color: whitesmoke;
      }
      &:nth-child(2) {
        background-color: #333;
      }
      &:nth-child(3) {
        background-color: darkblue;
      }
    }
  }
}

.link{
  text-decoration: none;
  color: white;
}
